import moment from "moment"

export function getDayString(day: string | number) {
    const date = moment(Number(day) * 86_400_000).utc()
    return date.format(moment().utc().isSame(date, 'month') ? 'D' : 'M/D')
}
export function getDayFullString(day: string | number) {
    return moment(Number(day) * 86_400_000).utc().format('YYYY-MM-DD(UTC)')
}


/**  */
export function sleep(time: number): Promise<void> {
    return new Promise((resolve) => setTimeout(resolve, time));
}

export function groupChartDate(chartData: any[], groupFields: string[], firstKey: string, secondKey: string) {
    const groupChartData = {}
    groupFields.forEach(k => { groupChartData[k] = {} })
    chartData.forEach(it => {
        groupFields.forEach(k => {
            const item = groupChartData[k]
            const firstValue = it[firstKey]
            const secondValue = it[secondKey];
            (item[firstValue] || (item[firstValue] = { [firstKey]: firstValue }))[secondValue] = it[k]
        })
    })
    groupFields.forEach(k => {
        groupChartData[k] = Object.values(groupChartData[k])
    })
    return groupChartData
}