export const TextInputs = props => {
    const { resource, source, keys, ...rest } = props;
    const perfix = source ? source + '.' : ''
    const sm = props.sm || (12 / keys.length) as GridSize
    const xs = props.xs || 12
    return (
        <Grid container spacing={1}>
            {keys.map(item => <Grid item xs={xs} sm={sm}><TextInput source={perfix + item} resource={resource} /> </Grid>)}
        </Grid>)
    /*return (
        <Box display="flex">
            {keys.map(item => <Box flex={1} mr="0.1em"><TextInput source={perfix + item} resource={resource}  /> </Box>)}
        </Box>
    );*/
};

