
import polyglotI18nProvider from 'ra-i18n-polyglot';
import chineseMessages from '../i18n/zh';
import englishMessages from '../i18n/en';
import { resolveBrowserLocale } from 'react-admin';
import moment from 'moment';
import 'moment/locale/zh-cn';

const messages = {
    zh: chineseMessages,
    en: englishMessages,
};


export const languageNames = {
    zh: '',
    en: 'English'
}

const languageToMomentLocale = {
    zh: 'zh-cn',
    en: 'en'
}

export const getLocale = () => {
    return localStorage.getItem('locale') || resolveBrowserLocale()
}

export const getMomentLocale = () => {
    const l = getLocale()
    return languageToMomentLocale[l] || l
}

// 
moment.locale(getMomentLocale())

/**  .*/
const onMissingKey = (key: string, options: any = {}) => {
    const index = key.lastIndexOf(".")
    if (index == -1) return key
    key = key.substring(index + 1)
    if (typeof options.__ === 'string') {
        options._ = options.__.substring(options.__.lastIndexOf(".") + 1)
        delete options.__
    }
    if (key.includes("showHero"))
        console.log(key, options)
    return translate(key, options)
}

export const i18nProvider = polyglotI18nProvider(
    locale => {
        return messages[locale] ? messages[locale] : messages.en
    }, getLocale(),
    { onMissingKey }
);

const translate = i18nProvider.translate
i18nProvider.translate = (key: string, options: any = {}) => {
    //if (key.includes('summary')) console.log(key)
    //key = key.replaceAll('metadata.', '')
    options.__ = options._
    delete options._

    if (key == null) return key

    if (key.includes("showHero"))
        console.log(key, options)
    return translate(key.replaceAll(/\[.*?\]/g, '_'), options)
}
