import * as React from "react";
import { Card, CardContent, CardHeader, Container, Box, Grid, TextField } from '@material-ui/core';
import { useTranslate } from "react-admin";
import moment from "moment";
import lodash from 'lodash';
import queryString from 'query-string'
import { format } from "d3-format";
import { getDayString, getDayFullString } from "../../utils";
import { adminApi } from "../../providers/dataProvider";
import { useMyContext } from "../../providers/contextProvider";
import MyChart from "../../components/MyChart";
import AnalyticsQuery from "../../components/AnalyticsQuery";

export default (props) => {
    const myContext = useMyContext()
    if (!myContext.state.clientKeys) return <Box />

    const query = Object.assign({
        dayBegin: moment.utc().startOf('day').subtract(14, 'days').format('YYYY-MM-DD'),
        dayEnd: moment.utc().startOf('day').format('YYYY-MM-DD'),
        channel: '',
        version: '',
        cloud: '',
        event: myContext.state.flatKeys.event[0]
    }, queryString.parse(props.location.search))

    const [state, setState] = React.useState<{ chartData: any, series: any[], tabs?: any[] }>({
        chartData: [],
        series: [],
        tabs: undefined,
    })

    const handleChange = async (event) => {
        query[event.target.name] = event.target.value
        props.history.push(props.location.pathname + '?' + queryString.stringify(query))
    }

    const updateApi = async () => {
        const firstKey = 'day'
        const filterKeys = ['channel', 'version', 'cloud']
        const seriesKeys = ['new', 'active', 'login']
        let secondKey
        const body: any = {
            dayEnd: moment.utc(query.dayEnd).unix() / 86_400,
            dayBegin: moment.utc(query.dayBegin).unix() / 86_400,
            values: [...seriesKeys, 'event'],
        }
    }

    React.useEffect((() => { updateApi() }), [myContext.state.clientKeys, ...Object.values(query)])

    const translate = useTranslate()
    return (
        <Box >
            <Container maxWidth="xl">
                <Grid item xs={12}>
                    <CardContent>
                        <AnalyticsQuery onChange={handleChange} values={query} />
                    </CardContent>
                </Grid>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Card>
                            <CardHeader title={translate('pos.funnelTrend')} />
                            <MyChart data={state.chartData} tabs={state.tabs} series={state.series}
                                argumentFormat={obj => getDayString}
                                tooltipTitleFormat={getDayFullString}
                            />
                        </Card>
                    </Grid>
                </Grid>
            </Container>
        </Box >
    )
}