import { TextField } from '@material-ui/core';
import Grid, { GridSize } from '@material-ui/core/Grid';
import { useTranslate } from "react-admin";
import lodash from 'lodash';
import { useMyContext } from '../providers/contextProvider';

export default (props) => {
    const { state } = useMyContext()
    const types = ['channel', 'version', 'cloud', 'event'].filter(t => props.values.hasOwnProperty(t))
    const md = ([12, 12, 6, 4, 6] as GridSize[])[types.length]
    const translate = useTranslate();
    const multipleType = types.find(t => lodash.get(state.clientKeys[t], props.values[t])?._children)
    return (
        <Grid container spacing={3}>
            {(props.values.dayBegin) ? (<Grid item md={6} xs={12} >
                <TextField id="dayBegin" name="dayBegin" onChange={props.onChange} type="date" label={translate('pos.dateFrom')} defaultValue={props.values.dayBegin} variant="outlined" required />
            </Grid>) : null}
            {(props.values.dayEnd) ? (<Grid item md={6} xs={12} >
                <TextField id="dayEnd" name="dayEnd" onChange={props.onChange} type="date" label={translate('pos.dateTo')} defaultValue={props.values.dayEnd} variant="outlined" required />
            </Grid>) : null}
            {types.map(type => {
                const item = state.flatKeys[type]
                const itemClientKeys = state.clientKeys[type]
                const itemValue = props.values[type]
                return (<Grid item md={md} xs={12} >
                    <TextField
                        fullWidth
                        label={translate('pos.select' + type.charAt(0).toUpperCase() + type.slice(1))}
                        name={type}
                        onChange={props.onChange}
                        select
                        SelectProps={{ native: true }}
                        value={itemValue}
                        variant="outlined"
                    >
                        {type != 'event' ? (<option key={''} value={''} ></option>) : null}
                        {item.map(key => {
                            const it = lodash.get(itemClientKeys, key)
                            return <option key={key} disabled={multipleType && multipleType != type && it?._children} value={key} >{it?._name || key}{it?._children?'...':''}</option>
                        })}
                    </TextField>
                </Grid>)
            })}
        </Grid>
    )
}