import * as React from "react";
import { Card, CardContent, CardHeader, Container, Box, Grid, TextField } from '@material-ui/core';
import { useTranslate } from "react-admin";
import moment from "moment";
import lodash from 'lodash';
import queryString from 'query-string'
import { format } from "d3-format";
import { getDayString, getDayFullString, groupChartDate } from "../../utils";
import { adminApi } from "../../providers/dataProvider";
import { useMyContext } from "../../providers/contextProvider";
import MyChart from "../../components/MyChart";
import AnalyticsQuery from "../../components/AnalyticsQuery";

export default (props) => {
    const myContext = useMyContext()
    if (!myContext.state.clientKeys) return <Box />

    const query = Object.assign({
        dayBegin: moment.utc().startOf('day').subtract(14, 'days').format('YYYY-MM-DD'),
        dayEnd: moment.utc().startOf('day').format('YYYY-MM-DD'),
        channel: '',
        version: '',
        cloud: ''
    }, queryString.parse(props.location.search))

    const [state, setState] = React.useState<{ chartData: any, chartData2: any, series: any[], tabs?: any[], tabs2?: any[] }>({
        chartData: [],
        chartData2: [],
        series: [],
        tabs: undefined,
        tabs2: undefined
    })

    const handleChange = async (event) => {
        query[event.target.name] = event.target.value
        props.history.push(props.location.pathname + '?' + queryString.stringify(query))
    }

    const updateApi = async () => {
        const filterKeys = ['channel', 'version', 'cloud']
        const seriesKeys = ['active', 'new', 'login']
        const secondKeys = {}
        const filterPaths = {}
        filterKeys.forEach(t => {
            const path = query[t] as any
            const children = lodash.get(myContext.state.clientKeys[t], path)?._children
            filterPaths[t] = path
            if (children) secondKeys[t] = children
        })
        const body: any = {
            dayEnd: moment.utc(query.dayEnd).unix() / 86_400,
            dayBegin: moment.utc(query.dayBegin).unix() / 86_400,
            keys: [...Object.keys(secondKeys)],
            values: ['region', 'lang'],
            ...filterPaths, ...secondKeys
        }

        const res = await adminApi.post("analytics", body)
        const chartData: any[] = []
        const chartData2: any[] = []
        res.forEach(item => {
            const {region, lang,...otherFields} = item
            Object.entries(region).forEach(it => {
                chartData.push({
                    ...otherFields,
                    region: it[0],
                    ...(it[1] as any)
                })
            })
            Object.entries(lang).forEach(it => {
                chartData2.push({
                    ...otherFields,
                    lang: it[0],
                    ...(it[1] as any)
                })
            })
        })
        chartData.sort((a, b) => a.active - b.active)
        chartData2.sort((a, b) => a.active - b.active)
        chartData.splice(0,chartData.length-32)
        chartData2.splice(0,chartData2.length-32)
        const seriesOrTabs = seriesKeys.map(t => ({ name: translate(`pos.chart.${t}`) || t, valueField: t, }))

        const secondKey = Object.keys(secondKeys)[0]
        if (!secondKey) {
            setState({ chartData, chartData2, series: seriesOrTabs })
        } else {
            console.log('chart', res)
            const newData = groupChartDate(chartData, seriesKeys, 'region', secondKey)
            const newData2 = groupChartDate(chartData2, seriesKeys, 'lang', secondKey)
            const cutNameStart = filterPaths[secondKey].length + 1
            const series = secondKeys[secondKey].map(t => ({
                name: lodash.get(myContext.state.clientKeys[secondKey], t)?._name || t.substring(cutNameStart),
                valueField: t
            }))
            setState({ chartData: newData, chartData2: newData2, series, tabs: seriesOrTabs, tabs2: seriesOrTabs })
        }
    }

    React.useEffect(() => { updateApi() }, [myContext.state.clientKeys, ...Object.values(query)])

    const translate = useTranslate()
    return (
        <Box >
            <Container maxWidth="xl">
                <Grid item xs={12}>
                    <CardContent>
                        <AnalyticsQuery onChange={handleChange} values={query} />
                    </CardContent>
                </Grid>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Card>
                            <CardHeader title={translate('pos.regionDist')} />
                            <MyChart data={state.chartData} tabs={state.tabs} series={state.series}
                                argumentFormat={obj => getDayString}
                                tooltipTitleFormat={getDayFullString}
                                category={'region'}
                                type={'bar'}
                                rotated
                            />
                        </Card>
                    </Grid>
                </Grid>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Card>
                            <CardHeader title={translate('pos.languageDist')} />
                            <MyChart data={state.chartData2} tabs={state.tabs2} series={state.series}
                                argumentFormat={obj => getDayString}
                                tooltipTitleFormat={getDayFullString}
                                category={'lang'}
                                type={'bar'}
                                rotated
                            />
                        </Card>
                    </Grid>
                </Grid>
            </Container>
        </Box >
    )
}