import * as React from "react";
import { Card, CardContent, CardHeader, Container, Box, Grid, TextField } from '@material-ui/core';
import { useTranslate } from "react-admin";
import moment from "moment";
import lodash from 'lodash';
import queryString from 'query-string'
import { format } from "d3-format";
import { getDayString, getDayFullString, groupChartDate } from "../../utils";
import { adminApi } from "../../providers/dataProvider";
import { useMyContext } from "../../providers/contextProvider";
import MyChart from "../../components/MyChart";
import AnalyticsQuery from "../../components/AnalyticsQuery";

export default (props) => {
    const myContext = useMyContext()
    if (!myContext.state.clientKeys) return <Box />

    const query = Object.assign({
        dayBegin: moment.utc().startOf('day').subtract(14, 'days').format('YYYY-MM-DD'),
        dayEnd: moment.utc().startOf('day').format('YYYY-MM-DD'),
        channel: '',
        version: '',
        cloud: '',
        event: myContext.state.flatKeys.event[0]
    }, queryString.parse(props.location.search))

    const [state, setState] = React.useState<{ chartData: any, series: any[], tabs?: any[] }>({
        chartData: [],
        series: [],
        tabs: undefined,
    })

    const handleChange = async (event) => {
        query[event.target.name] = event.target.value
        props.history.push(props.location.pathname + '?' + queryString.stringify(query))
    }

    const updateApi = async () => {
        const firstKey = 'day'
        const filterKeys = ['channel', 'version', 'cloud']
        const seriesKeys = ['active']
        const eventKey = 'event.' + query.event
        const secondKeys = {}
        const filterPaths = {}
        filterKeys.forEach(t => {
            const path = query[t] as any
            const children = lodash.get(myContext.state.clientKeys[t], path)?._children
            filterPaths[t] = path
            if (children) secondKeys[t] = children
        })
        const body: any = {
            dayEnd: moment.utc(query.dayEnd).unix() / 86_400,
            dayBegin: moment.utc(query.dayBegin).unix() / 86_400,
            keys: [firstKey, ...Object.keys(secondKeys)],
            values: [...seriesKeys, eventKey],
            ...filterPaths, ...secondKeys
        }

        const multipleEventKeys = lodash.get(myContext.state.clientKeys.event, query.event)?._children

        const res = await adminApi.post("analytics", body)
        const seriesOrTabs = [
            { name: translate('pos.chart.totalValue'), valueField: "value" },
            { name: translate('pos.chart.totalTimes'), valueField: "count" },
            { name: translate('pos.chart.active'), valueField: "active" },
            { name: translate('pos.chart.valuePerTime'), valueField: "vpt" },
            { name: translate('pos.chart.valuePerUser'), valueField: "vpu" },
            { name: translate('pos.chart.timesPerUser'), valueField: "tpu" },
        ]
        const chartData: any[] = []
        if (!multipleEventKeys) {
            res.forEach(it => {
                const { event, ...itemData } = it
                const eventValue = lodash.get(event, query.event)
                if (eventValue) {
                    chartData.push({
                        ...itemData,
                        value: eventValue._value,
                        count: eventValue._count,
                        vpt: eventValue._value / eventValue._count,
                        vpu: eventValue._value / it.active,
                        tpu: eventValue._count / it.active
                    })
                }
            })
        } else {
            secondKeys['event'] = multipleEventKeys
            filterPaths['event'] = query.event
            res.forEach(it => {
                const eventValue = it.event
                const active = it['active']
                const firstValue = it[firstKey]
                multipleEventKeys.forEach(k => {
                    const item = lodash.get(eventValue, k)
                    if (item)
                        chartData.push({
                            [firstKey]: firstValue,
                            event: k,
                            value: item._value,
                            count: item._count,
                            active,
                            vpt: item._value / item._count,
                            vpu: item._value / active,
                            tpu: item._count / active
                        })
                })
            })
        }
        const secondKey = Object.keys(secondKeys)[0]
        if (!secondKey) {
            setState({ chartData, series: seriesOrTabs })
        } else {
            const groupFields = ["value", "count", "active", "vpt", "vpu", "tpu"]
            const newData = groupChartDate(chartData, groupFields, firstKey, secondKey)
            const cutNameStart = filterPaths[secondKey].length + 1
            const series = (secondKeys[secondKey] as string[]).map(t => ({
                name: lodash.get(myContext.state.clientKeys[secondKey], t)?._name || t.substring(cutNameStart),
                valueField: t
            }))
            console.log('xxxx', newData, chartData, groupFields, firstKey, secondKey)
            setState({ chartData: newData, series, tabs: seriesOrTabs })
        }
    }

    React.useEffect((() => { updateApi() }), [myContext.state.clientKeys, ...Object.values(query)])

    const translate = useTranslate()
    return (
        <Box >
            <Container maxWidth="xl">
                <Grid item xs={12}>
                    <CardContent>
                        <AnalyticsQuery onChange={handleChange} values={query} />
                    </CardContent>
                </Grid>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Card>
                            <CardHeader title={translate('pos.eventTrend')} />
                            <MyChart data={state.chartData} tabs={state.tabs} series={state.series}
                                argumentFormat={obj => getDayString}
                                tooltipTitleFormat={getDayFullString}
                                category={'day'}
                            />
                        </Card>
                    </Grid>
                </Grid>
            </Container>
        </Box >
    )
}