import * as React from 'react';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import LabelIcon from '@material-ui/icons/Label';
import { makeStyles } from '@material-ui/core/styles';
import {
    useTranslate,
    DashboardMenuItem,
    MenuItemLink,
    MenuProps,
} from 'react-admin';

/*import visitors from '../visitors';
import orders from '../orders';
import invoices from '../invoices';
import products from '../products';
import categories from '../categories';
import reviews from '../reviews';*/
import rooms from '../pages/datas/rooms';
import users from '../pages/datas/users';
import guilds from '../pages/datas/guilds';
import leaderboards from '../pages/datas/leaderboards';
import battleRecords from '../pages/datas/battleRecords';
import reportLogs from '../pages/datas/reportLogs';
import configs from '../pages/datas/configs';

import SubMenu from './SubMenu';
import { AppState } from '../types';

import OrderIcon from '@material-ui/icons/AttachMoney';

import AnalyticsIcon from '@material-ui/icons/Timeline';
import UsersIcon from '@material-ui/icons/SupervisedUserCircle';
import RealtimeIcon from '@material-ui/icons/Timer';
import EventsIcon from '@material-ui/icons/Event';
import FunnelsIcon from '@material-ui/icons/FilterList';
import PersonasIcon from '@material-ui/icons/PersonPin';
import KeysIcon from '@material-ui/icons/Spellcheck';
import { appApi } from '../providers/dataProvider';
import { useMyContext } from '../providers/contextProvider';

type MenuName = 'menuCatalog' | 'menuSales' | 'menuCustomers' | 'menuAnalytics' | 'menuData' | 'menuAi' | 'menuLogs';

const Menu = ({ dense = false }: MenuProps) => {

    const { state } = useMyContext()

    const [menus, setMenus] = useState({
        menuCatalog: true,
        menuSales: true,
        menuCustomers: true,
        menuAnalytics: true,
        menuData: true,
        menuAi: true,
        menuLogs: true,
        //appConfigId: 'config/app'

    });

    //React.useEffect((async () => setMenus({ ...menus, appConfigId: (await appApi.post("config")).appConfigId })) as any, []);


    const translate = useTranslate();
    useSelector((state: AppState) => state.theme); // force rerender on theme change
    const classes = useStyles();

    const handleToggle = (menu: MenuName) => {
        setMenus(menus => ({ ...menus, [menu]: !menus[menu] }));
    };

    return (
        <div className={classes.root}>
            {' '}
            <DashboardMenuItem />
            <SubMenu
                handleToggle={() => handleToggle('menuAnalytics')}
                isOpen={menus.menuAnalytics}
                name="pos.menu.analytics"
                icon={<AnalyticsIcon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/analytics/realtime`}
                    primaryText={translate(`analytics.realtime.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<RealtimeIcon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/analytics/users`}
                    primaryText={translate(`analytics.users.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<UsersIcon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/analytics/events`}
                    primaryText={translate(`analytics.events.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<EventsIcon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/analytics/funnels`}
                    primaryText={translate(`analytics.funnels.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<FunnelsIcon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/analytics/personas`}
                    primaryText={translate(`analytics.personas.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<PersonasIcon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/analytics/keys`}
                    primaryText={translate(`analytics.keys.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<KeysIcon />}
                    dense={dense}
                />
            </SubMenu>
            {state.config.roomConsoleUrl ? <SubMenu
                handleToggle={() => handleToggle('menuData')}
                isOpen={menus.menuData}
                name="pos.menu.data"
                icon={<rooms.icon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/${rooms.name}`}
                    primaryText={translate(`resources.${rooms.name}.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<rooms.icon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/${users.name}`}
                    primaryText={translate(`resources.${users.name}.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<users.icon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/${guilds.name}`}
                    primaryText={translate(`resources.${guilds.name}.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<guilds.icon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/${leaderboards.name}`}
                    primaryText={translate(`resources.${leaderboards.name}.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<leaderboards.icon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/${battleRecords.name}`}
                    primaryText={translate(`resources.${battleRecords.name}.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<battleRecords.icon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/${reportLogs.name}`}
                    primaryText={translate(`resources.${reportLogs.name}.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<reportLogs.icon />}
                    dense={dense}
                />
            </SubMenu> : null}
            <MenuItemLink
                to={`/appConfig`}
                primaryText={translate(`pos.menu.appConfig`, {
                    smart_count: 2,
                })}
                leftIcon={<configs.icon />}
                dense={dense}
            />
            {/*<SubMenu
                handleToggle={() => handleToggle('menuAi')}
                isOpen={menus.menuAi}
                name="pos.menu.ai"
                icon={<configs.icon />}
                dense={dense}
            >
               <MenuItemLink
                    to={`/config/${menus.appConfigId}`}
                    primaryText={translate(`pos.menu.Config`, {
                        smart_count: 2,
                    })}
                    leftIcon={<configs.icon />}
                    dense={dense}
                />
            </SubMenu>*/}
            <SubMenu
                handleToggle={() => handleToggle('menuLogs')}
                isOpen={menus.menuLogs}
                name="pos.menu.logs"
                icon={<reportLogs.icon />}
                dense={dense}
            >
            </SubMenu>
            { /*<SubMenu
                handleToggle={() => handleToggle('menuSales')}
                isOpen={menus.menuSales}
                name="pos.menu.sales"
                icon={<OrderIcon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/commands`}
                    primaryText={translate(`resources.commands.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<OrderIcon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/invoices`}
                    primaryText={translate(`resources.invoices.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<OrderIcon />}
                    dense={dense}
                />
            </SubMenu>
            <SubMenu
                handleToggle={() => handleToggle('menuCatalog')}
                isOpen={menus.menuCatalog}
                name="pos.menu.catalog"
                icon={<products.icon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/products`}
                    primaryText={translate(`resources.products.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<products.icon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/categories`}
                    primaryText={translate(`resources.categories.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<categories.icon />}
                    dense={dense}
                />
            </SubMenu>
            <SubMenu
                handleToggle={() => handleToggle('menuCustomers')}
                isOpen={menus.menuCustomers}
                name="pos.menu.customers"
                icon={<visitors.icon />}
                dense={dense}
            >
                <MenuItemLink
                    to={`/customers`}
                    primaryText={translate(`resources.customers.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<visitors.icon />}
                    dense={dense}
                />
                <MenuItemLink
                    to={`/segments`}
                    primaryText={translate(`resources.segments.name`, {
                        smart_count: 2,
                    })}
                    leftIcon={<LabelIcon />}
                    dense={dense}
                />
            </SubMenu>
            <MenuItemLink
                to={`/reviews`}
                primaryText={translate(`resources.reviews.name`, {
                    smart_count: 2,
                })}
                leftIcon={<reviews.icon />}
                dense={dense}
            />*/}
        </div>
    );
};

const useStyles = makeStyles(theme => ({
    root: {
        marginTop: theme.spacing(1),
    },
}));

export default Menu;
