import { fetchUtils } from 'react-admin'
import { stringify } from 'query-string';

export default class ApiProvider {
    name: string
    apiUrl: string
    private _profile: { [name: string]: any } = {}

    get token() { return this._profile.token }
    set token(value) {
        if (this._profile.token == value) return
        this._profile.token = value
        if (value) localStorage.setItem(this.name + '_token', value)
        else localStorage.removeItem(this.name + '_token')
    }

    public setProfile(key: string, value: any) { this._profile[key] = value }
    public getProfile(key: string) { return this._profile[key] }

    constructor(apiUrl: string, name: string) {
        this.apiUrl = apiUrl
        this.name = name
        this._profile.token = localStorage.getItem(this.name + '_token')
    }

    async request(path: string, query?: any, body?: any, method?: string) {
        let url: string = `${this.apiUrl}/${path}`
        const isGet = !method || method == 'GET'
        if (isGet || query) url = `${url}?${stringify(Object.assign({ ...this._profile }, query))}`
        const res = await fetchUtils.fetchJson(url, isGet ? undefined : {
            method,
            body: JSON.stringify(Object.assign({ ...this._profile }, body)),
        })
        return res.json
    }

    async get(url: string, query?: any): Promise<any> { return await this.request(url, query) }
    async post(url: string, body?: any): Promise<any> { return await this.request(url, undefined, body, 'POST') }
    async put(url: string, query?: any, body?: any): Promise<any> { return await this.request(url, query, body, 'PUT') }
    async delete(url: string, query?: any, body?: any): Promise<any> { return await this.request(url, query, body, 'DELETE') }
}