import * as React from "react";
import {
    List, Datagrid, TextField, EditButton, Edit,
    ListProps, TextInput, NumberField, BooleanField, BooleanInput, NumberInput, ArrayInput, SimpleFormIterator, DateField, DateInput, DateTimeInput, FormTab, TabbedForm, AutocompleteArrayInput, SimpleForm, FunctionField,
} from 'react-admin';
import { Typography, Box } from '@material-ui/core';
import { useMediaQuery } from '@material-ui/core';
import { TextInputs, NumberInputs, ElapsedTimeField } from '../../components/MyComponents';
import get from 'lodash/get';
import icon from '@material-ui/icons/BugReport';

const userFilters = [
    <TextInput source="source" alwaysOn allowEmpty />,
    <TextInput source="condition" alwaysOn allowEmpty />,
    <TextInput source="stackTrace" allowEmpty />,
    <TextInput source="version" allowEmpty />,
    <DateInput source="updatedAt" allowEmpty />,
];

//const renderStackTrace = (record, source) => (get(record, source) as string).replaceAll(\\g, '');
const ResList = (props: ListProps) => (
    <List filters={userFilters} {...props}>
        <Datagrid>
            <TextField source="source" />
            <TextField source="condition" />
            <FunctionField source="stackTrace" render={(record, source) => {
                const text = get(record, source) as string;
                const etc = /^(.*?)\(at <00000000000000000000000000000000>:0\)/.exec(text)?.[1]
                return etc ? etc + "..." : text
            }} />
            <TextField source="version" />
            <NumberField source="count" />
            <DateField source="updatedAt" />
            <EditButton />
        </Datagrid>
    </List>
);

const ReportLogTitle = ({ record }) => {
    return <span>ReportLog {record ? `"${record.condition}"` : ''}</span>;
};

const ResEdit = props => {
    const isSmall = useMediaQuery((theme: any) => theme.breakpoints.down('sm'));
    return (
        <Edit title={<ReportLogTitle {...props} />} {...props}>
            <SimpleForm>
                <DateInput source="date" />
                <TextInput source="source" />
                <TextInput source="condition" />
                <TextInput source="stackTrace" multiline />
                <TextInput source="version" />
                <NumberInput source="count" />
                <DateTimeInput source="createdAt" />
                <DateTimeInput source="updatedAt" />
            </SimpleForm>
        </Edit>
    );
}

export default { name: "reportLog", list: ResList, edit: ResEdit, icon }