export const adminApi = new ApiProvider('https://www.cyberpunk.online/api/admin', 'admin')
export const appApi = new ApiProvider('', 'app')

const _id2id = (json: any) => {
    if (json == null) return json
    if (Array.isArray(json)) {
        json.forEach(t => {
            t.id = t._id
            delete t._id
            delete t.friendIds
            delete t.blockedUserIds
        })
    } else if (json.hasOwnProperty('_id') && !json.hasOwnProperty('id')) {
        json.id = json._id
        delete json._id
        delete json.friendIds
        delete json.blockedUserIds
    }
    return json
}

const id2_id = (json: any) => {
    if (json == null) return json
    if (Array.isArray(json)) {
        json.forEach(t => {
            t._id = t.id
            delete t.id
        })
    } else if (json.hasOwnProperty('id') && !json.hasOwnProperty('_id')) {
        json._id = json.id
        delete json.id
    }
    return json
}

// This feature is used internally to support nested phrase objects.
const flatten = function (obj) {
    const result = {};
    function recurse(src, prefix) {
        for (let key in src) {
            const value = src[key];
            key = prefix + key;
            if (typeof value === 'object') {
                recurse(value, key + '.');
            } else {
                result[key] = value;
            }
        }
    }
    recurse(obj, '');
    return result;
}

const filterStringify = (filter) => {
    //typeof value === "string" && key !== "_id" && key[0] !== "$" ? { "$regex": value, "$options": "i" } : value
    // JSON.stringify(id2_id(filter), (key, value) =>  {  console.log(key, value,typeof value); return value})
    return JSON.stringify(flatten(id2_id(filter)), (key, value) => {
        if (typeof value !== "string") return value
        if (key === "_id") return value
        if (key[0] === "$") return value
        if (isNaN(Number(value)) && !isNaN(Date.parse(value))) return value
        return { "$regex": value, "$options": "i" }
    })
}

const dataProvider = {
    getList: async (resource, params) => {
        const { page, perPage } = params.pagination;
        const { field, order } = params.sort;
        //const filter = { "$regex": params.filter }//Object.fromEntries(Object.entries(params.filter).map(t => [t[0], t[0] === "id" ? t[1] : { "$regex": t[1], "$options": "i" }]))
        const query = {
            sort: JSON.stringify(id2_id({ [field]: order })),
            limit: perPage,
            skip: (page - 1) * perPage,
            query: filterStringify(params.filter),
            select: "-metadata.battleList"
        };

        const ret1 = await appApi.get(resource, query)
        const ret2 = await appApi.get(`${resource}/count`, { query: query.query })
        return { data: _id2id(ret1), total: ret2.count }
    },

    getOne: async (resource, params) => ({ data: _id2id(await appApi.get(`${resource}/${params.id}`)) }),
    getMany: async (resource, params) => ({ data: await appApi.get(resource, { query: JSON.stringify({ _id: { $in: params.ids } }), select: "-metadata.battleList" }) }),

    getManyReference: async (resource, params) => {
        const { page, perPage } = params.pagination;
        const { field, order } = params.sort;
        const query = {
            sort: JSON.stringify(id2_id({ [field]: order })),
            limit: perPage,
            skip: (page - 1) * perPage,
            query: filterStringify({ ...params.filter, [params.target]: params.id, }),
            select: "-metadata.battleList"
        };

        const ret1 = await appApi.get(resource, query)
        const ret2 = await appApi.get(`${resource}/count`, { query: query.query })
        return { data: _id2id(ret1), total: ret2.count }

        /*const url = `${resource}?${stringify(query)}`;
        return fetchApi(url).then(({ headers, json }) => ({
            data: _id2id(json),
            total: parseInt(headers.get('content-range')?.split('/').pop() || "", 10),
        }));*/
    },

    update: async (resource, params) => ({ data: await appApi.put(`${resource}/${params.id}`, undefined, params.data) }),
    updateMany: async (resource, params) => ({ data: await appApi.put(resource, { query: JSON.stringify({ _id: { $in: params.ids } }) }, params.data) }),
    create: async (resource, params) => ({ data: { ...params.data, id: (await appApi.post(resource, params.data)).id } }),
    delete: async (resource, params) => ({ data: await appApi.delete(`${resource}/${params.id}`) }),
    deleteMany: async (resource, params) => ({ data: await appApi.delete(resource, { query: JSON.stringify({ _id: { $in: params.ids } }) }) }),
};

export default dataProvider