import * as React from "react";
import { Card, CardContent, CardHeader } from '@material-ui/core';
//import Tabs from '@mui/material/Tabs';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import CardMedia from '@material-ui/core/CardMedia';
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import Slider from '@material-ui/core/Slider';
import { NumberField, NumberInput, useTranslate } from "react-admin";
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import RoomIcon from '@material-ui/icons/SportsEsports';
import CpuIcon from '@material-ui/icons/Memory';
import MemoryIcon from '@material-ui/icons/Storage';
import UserIcon from '@material-ui/icons/Group';
import GuildIcon from '@material-ui/icons/Flag';
import RecordIcon from '@material-ui/icons/History';
import ReportLogIcon from '@material-ui/icons/BugReport';
import { withStyles, makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import { red, green, blue, yellow, deepOrange, orange } from '@material-ui/core/colors';
import { adminApi, appApi } from "../../providers/dataProvider";
import MyChart from "../../components/MyChart";
//import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend as Relegend, ResponsiveContainer } from 'recharts';
import { energyConsumption as data } from '../../components/data-vizualization';
import moment from "moment";
import { sleep } from "../../utils";

export default () => {
    const [state, setState] = React.useState<{ realtime: any }>({
        realtime: [],
    });

    interface RealtimeItem { hours: number, login: number, new: number, active: number }
    let realtimeData: RealtimeItem[]

    const updateRealtime = async (hours: number[]) => {
        const hoursEnd = moment().startOf('hour').unix() / 3600
        const res: RealtimeItem[] = await adminApi.post("realtime", { hours })
        if (!realtimeData) realtimeData = res
        else {
            for (const it of res) {
                const index = realtimeData.findIndex(t => t.hours == it.hours)
                if (index != -1) realtimeData.splice(index, 1, it)
                else realtimeData.push(it)
            }
        }
        const realtime = { login: [] as any[], new: [] as any[], active: [] as any[] }
        for (let i = 0; i <= 24; ++i) {
            const hours = hoursEnd - i
            realtime.login[i] = { hours }
            realtime.new[i] = { hours }
            realtime.active[i] = { hours }
        }
        const today = realtimeData.filter(t => t.hours >= hoursEnd - 24)
        for (const it of today) {
            const index = hoursEnd - it.hours
            realtime.login[index].today = it.login
            realtime.new[index].today = it.new
            realtime.active[index].today = it.active
        }
        const yesterday = realtimeData.filter(t => t.hours <= hoursEnd - 24 && t.hours >= hoursEnd - 48)
        for (const it of yesterday) {
            const index = hoursEnd - 24 - it.hours
            realtime.login[index].yesterday = it.login
            realtime.new[index].yesterday = it.new
            realtime.active[index].yesterday = it.active
        }
        const lastWeek = realtimeData.filter(t => t.hours <= hoursEnd - 168 && t.hours >= hoursEnd - 192)
        for (const it of lastWeek) {
            const index = hoursEnd - 168 - it.hours
            realtime.login[index].lastWeek = it.login
            realtime.new[index].lastWeek = it.new
            realtime.active[index].lastWeek = it.active
        }
        setState({ realtime })

        setTimeout(() => {

        }, 5000);
    }

    React.useEffect(() => {
        let stop = false
        const update = async () => {
            const hours: number[] = []
            const hoursEnd = moment().startOf('hour').unix() / 3600
            for (let i = 0; i <= 48; ++i) hours.push(hoursEnd - i)
            for (let i = 0; i <= 48; ++i) hours.push(hoursEnd - i - 168)
            await updateRealtime(hours)
            while (!stop) {
                await sleep(5000)
                await updateRealtime([moment().startOf('hour').unix() / 3600])
            }
        }
        update()
        return () => { stop = true };//
    }, []);

    const translate = useTranslate();
    return (
        <Card>
            <CardHeader title={translate('pos.realtimeTrend')} />
            <MyChart data={state.realtime} tabs={[
                { name: translate('pos.chart.login'), valueField: "login", argumentField: 'hours' },
                { name: translate('pos.chart.new'), valueField: "new", argumentField: 'hours' },
                { name: translate('pos.chart.active'), valueField: "active", argumentField: 'hours' },
            ]} series={[
                { name: translate('pos.chart.today'), valueField: "today", argumentField: 'hours' },
                { name: translate('pos.chart.yesterday'), valueField: "yesterday", argumentField: 'hours' },
                { name: translate('pos.chart.lastWeek'), valueField: "lastWeek", argumentField: 'hours' },
            ]}
                argumentFormat={obj => tick => (new Date(Number(tick) * 3_600_000)).getHours().toString()}
                tooltipTitleFormat={tick => (new Date(Number(tick) * 3_600_000)).getHours().toString()+':00'}
            />
        </Card>
    );
}