import * as React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import AppsIcon from '@material-ui/icons/Apps';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { adminApi } from "../providers/dataProvider";
import { useMyContext } from '../providers/contextProvider';

const AppSwitcher = () => {
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const { state, setDefaultApp, setApps, setKeys, setConfig } = useMyContext()

    const getAppList = async () => {
        if (!adminApi.token) return
        setApps(await adminApi.post("appList"))
        setKeys(await adminApi.post("getKeys"))
        setConfig(await adminApi.post("getConfig"))
    }

    React.useEffect(getAppList as any, []);

    return (
        <div>
            <Button aria-controls="apps-menu" aria-haspopup="true" color="inherit" onClick={(event) => setAnchorEl(event.currentTarget)}>
                <AppsIcon />{state.defaultApp}<ExpandMoreIcon />
            </Button>
            <Menu
                id="apps-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={() => setAnchorEl(null)}
            >
                {state.apps?.map(t =>
                    <MenuItem
                        onClick={() => {
                            setAnchorEl(null); setDefaultApp(t.name)
                            window.location.reload();
                        }}
                        selected={state.defaultApp === t.name}
                    >
                        {t.name}
                    </MenuItem>)}
            </Menu>
        </div>
    );
};

export default AppSwitcher;