import React from 'react';
import { withStyles, Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Box from '@material-ui/core/Box';
import Hidden from '@material-ui/core/Hidden';
import Paper from '@material-ui/core/Paper';
import { useTranslate } from 'react-admin';
import { format } from "d3-format";
import { getDayString } from "../utils";
import moment from 'moment';

const StyledTableCell = withStyles((theme: Theme) =>
    createStyles({
        /*head: {
            backgroundColor: theme.palette.common.black,
            color: theme.palette.common.white,
        },*/
        root: {
            paddingTop: theme.spacing(1),
            paddingBottom: theme.spacing(1),
            textAlign: 'center',
        },
    }),
)(TableCell);

const StyledTableRow = withStyles((theme: Theme) =>
    createStyles({ root: { '&:nth-of-type(odd)': { backgroundColor: theme.palette.action.hover, }, }, }),
)(TableRow);

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        table: {
            // minWidth: 700,
        },
    }));

const formatRetention = format(".1%")
export default function RetentionTable(props: { data: any }) {
    const { data } = props;
    const classes = useStyles();

    const series = [1, 2, 3, 4, 5, 6, 7, 14, 30]
    const xsSeries = [1, 2, 3, 7, 14]
    const today = moment.utc().startOf('day').unix() / 86_400
    const translate = useTranslate();//.map((it, index) => { xsSeries.includes(index) index })}
    return (
        <TableContainer >
            <Divider />
            <Table padding='none' aria-label="customized table">
                <TableHead>
                    <TableRow>
                        <StyledTableCell >{translate('pos.retention.date')}</StyledTableCell>
                        {series.map((it) => <Hidden smDown={!xsSeries.includes(it)}>
                            <StyledTableCell align="right">{translate('pos.retention.d' + it)}</StyledTableCell>
                        </Hidden>)}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {data.map((item) => {
                        if (today <= item.day) return
                        return <StyledTableRow key={item.day}>
                            <StyledTableCell component="th" scope="row"> {getDayString(item.day)} </StyledTableCell>
                            {series.map(it => <Hidden smDown={!xsSeries.includes(it)}>
                                {(() => {
                                    if (today < item.day + it) return <StyledTableCell align="right"></StyledTableCell>
                                    const r = Math.min(1, (item[`new_d${it}`]||0) / (item.new || 1))
                                    return <StyledTableCell style={{ backgroundColor: `rgba(0, 0, 64, ${r * 0.5})` }} align="right">
                                        {formatRetention(r)?.padStart(5, '0')}</StyledTableCell>
                                })()}
                            </Hidden>)}
                        </StyledTableRow>
                    })}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
