import * as React from 'react';
import { Card, CardContent, CardHeader, Grid, Paper, Typography, Box, Button, ButtonGroup } from '@material-ui/core';
import { withStyles, createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox, { CheckboxProps } from '@material-ui/core/Checkbox';
import Chip from '@material-ui/core/Chip';
import CheckBoxIcon from '@material-ui/icons/FiberManualRecord';
import { Styles } from '@material-ui/core/styles/withStyles';
import {
    Chart as ReactChart,
    AreaSeries,
    ArgumentAxis,
    ValueAxis,
    ValueAxisProps,
    BarSeries,
    LineSeries,
    SplineSeries,
    Title,
    Legend,
    Tooltip,
    ScatterSeries,
    ZoomAndPan
} from '@devexpress/dx-react-chart-material-ui';
import {
    ArgumentScale,
    Animation,
    PathFn,
    PointComponentProps,
    PathPoints,
    EventTracker,
    HoverState,
    ScaleObject,
    Stack
} from '@devexpress/dx-react-chart';
import { connectProps } from '@devexpress/dx-react-core';
import {
    curveCatmullRom,
    line,
} from 'd3-shape';
import { scalePoint } from 'd3-scale';
import { format } from "d3-format";

import { energyConsumption as data } from './data-vizualization';
import { ComponentType } from 'react';
import { resolveBrowserLocale, useLocale } from 'react-admin';
import { getLocale } from './polyglot';

const Line = (props: LineSeries.PathSeriesProps) => (
    <LineSeries.Path
        {...props}
        path={(line<PointComponentProps>())
            .x(({ arg }) => arg)
            .y(({ val }) => val)
            .curve(curveCatmullRom) as any}
    />
);

const titleStyles: Styles<Theme, {}, "title"> = {
    title: {
        textAlign: 'center',
        width: '100%',
        marginBottom: '10px',
    },
};
const Text = withStyles(titleStyles)((props: Title.TextProps & { classes: any }) => {
    const { text, classes } = props;
    const [mainText, subText] = text.split('\\n');
    return (
        <div className={classes.title}>
            <Typography component="h3" variant="h5">
                {mainText}
            </Typography>
            <Typography variant="subtitle1">{subText}</Typography>
        </div>
    );
});

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            display: 'flex',
            justifyContent: 'center',
            flexWrap: 'wrap',
            '& > *': {
                margin: theme.spacing(0.5),
            },
        },
    }),
);

const patchProps = ({ hoverIndex, ...props }) => ({
    state: props.index === hoverIndex ? "hovered" : null,
    ...props
});

const BarPoint = (props) => <BarSeries.Point  {...patchProps(props) as any} />
const SplinePoint = (props) => <ScatterSeries.Point point={{ size: 5 }} {...patchProps(props) as any} />
const AreaPoint = (props) => {
    const patched = patchProps(props);
    return <ScatterSeries.Point point={patched.state ? { size: 7 } : { size: 0 }} {...patched as any} />
}

const SplineWithPoints = (props) => <React.Fragment> <SplineSeries.Path {...props} /><ScatterSeries.Path {...props} /></React.Fragment>
const AreaWithPoints = ({ state, ...props }: any) => <React.Fragment> <AreaSeries.Path {...props} /> <ScatterSeries.Path {...props} /> </React.Fragment>

const TooltipContent = ({ data, series, shows, format, text, style, ...props }: Tooltip.ContentProps & { data: any[], series: any[], shows: any, format?: (tick: string) => string, style?: React.CSSProperties }) => {
    const alignStyle = { ...style, paddingLeft: "10px" };
    const tooltipFormat = format || (val => val ? formatCount(val) : "N/A")
    const items = series.map((t, index) => {
        if (!shows[t.name]) return null
        const { name, valueField, color } = t
        const val = data[valueField];
        return (
            <tr key={index}>
                <td><svg width="10" height="10"><circle cx="5" cy="5" r="5" fill={color || palette[index % palette.length]} /></svg></td>
                <td><Tooltip.Content style={alignStyle} text={name} {...props} />
                </td><td align="right"><Tooltip.Content style={alignStyle} text={tooltipFormat(val)}  {...props} /></td>
            </tr>
        );
    });
    return <table>
        {items}
    </table>;
}

const getHoverIndex = ({ target }) => (target ? target.point : -1);

const formatCount = format(",")
const formatW = tick => Math.abs(tick) >= 10000 ? (tick / 10000).toFixed(tick >= 100000 ? 0 : 1) + 'w' : tick
const formatK = format(".2s")

const palette = ['#40C4FF', '#FF5252', '#00C853', '#FFEB3B', '#FF4081', '#E040FB']
export default class MyChart extends React.PureComponent<{
    classes?: { chart: string },
     data: any, 
    rotated?: boolean,
    zoom?: boolean,
    type?: string, category?: string, tabs?: any[], series: any[],
    valueFormat?: (scale: ScaleObject) => (tick: string) => string,
    argumentFormat?: (scale: ScaleObject) => (tick: string) => string,
    tooltipFormat?: (tick: string) => string
    tooltipTitleFormat?: (tick: string) => string
}, { tab: number, shows: any, target: any }> {
    TooltipContent: any;
    data: any[] = []
    chartData: any[] = []
    chartSeries: any[] = []
    seriesList: any[] = []
    BarPoint: any
    SplinePoint: any
    AreaPoint: any

    LegendRoot: any
    LegendLabel: any
    LegendItem: any
    LegendMarker: any

    changeHoverTime: NodeJS.Timeout | null = null

    constructor(props) {
        super(props)
        this.state = { tab: 0, shows: {}, target: null }
        this.createLegend()
        this.createTooltip()
    }

    changeHover = (target: any) => {
        //if (this.changeHoverTime)
        //    clearTimeout(this.changeHoverTime)
        //this.changeHoverTime = setTimeout(() => {
        if (!target) {
            if (!this.state.target) return
            target = null
        } else {
            if (this.state.target && this.state.target.series == target.series && this.state.target.point == target.point) return
            target = { series: target.series, point: target.point }
        }
        this.setState({ target })
        this.changeHoverTime = null
        //}, 50);
    }

    switchShow = (name: string) => {
        const shows = JSON.parse(JSON.stringify(this.state.shows))
        shows[name] = !shows[name]
        if (this.props.series.find(t => shows[t.name]))
            this.setState({ shows })
    }

    componentDidUpdate(prevProps, prevState) {
        console.log("chart", "componentDidUpdate")
        if (getHoverIndex(prevState) !== getHoverIndex(this.state)) {
            this.BarPoint.update();
            this.SplinePoint.update();
            this.AreaPoint.update();
            this.TooltipContent.update();
        }

        if (this.props.series.length > 0 && !this.props.series.find(t => this.state.shows[t.name])) {
            this.switchShow(this.props.series[0].name)
            return
        }

        if (JSON.stringify(prevState.shows) !== JSON.stringify(this.state.shows)) {
            console.log("chart", "createSeries")
            this.createSeries()
            this.forceUpdate()
        }
    }

    createLegend() {
        this.LegendRoot = props => (<Legend.Root {...props} style={{ display: 'grid', gridTemplateColumns: 'auto auto auto', margin: 'auto', }} />)
        this.LegendItem = props => (<Legend.Item {...props} style={{ flexDirection: 'column-reverse', paddingLeft: '0', paddingRight: '0' }} />)
        this.LegendLabel = props => {
            const { text: name } = props;
            return (<Legend.Label onClick={() => { this.switchShow(name.toString()) }}
                {...props} style={{ whiteSpace: 'nowrap', opacity: this.state.shows[name] ? 1 : 0.5 }} />)
        }
        this.LegendMarker = props => {
            const { className, name, color } = props;
            return (<svg className={className} fill={color} width="10" height="10"
                onClick={() => { this.switchShow(name) }}>
                <circle cx="5" cy="5" r="5" fill={color} fillOpacity={this.state.shows[name] ? 1 : 0.5} />
            </svg>)
        }
    }

    componentWillUnmount() {
        console.log("chart", "componentWillUnmount")
        this.setState({ target: null })
    }

    createTooltip() {
        const getHoverProps = () => ({
            hoverIndex: getHoverIndex(this.state)
        });
        this.BarPoint = connectProps(BarPoint, getHoverProps);
        this.SplinePoint = connectProps(SplinePoint, getHoverProps);
        this.AreaPoint = connectProps(AreaPoint, getHoverProps);

        this.TooltipContent = connectProps(TooltipContent, () => {
            //const { data, target } = this.state;
            return {
                data: this.state.target ? this.data[this.state.target.point] : null,
                series: this.props.series,
                shows: this.state.shows,
                format: this.props.tooltipFormat,
            };
        });
    }

    createSeries() {
        /* this.chartSeries = nextProps.series.map(({ key, name, valueField, argumentField, color, show, type, scaleName }) => ({
             key: key || name,
             name,
             valueField: valueField,
             argumentField: argumentField || "category",
             //color,
             scaleName,
             show,
             seriesComponent: SplineWithPoints,
             pointComponent: this.SplinePoint
         }))
         

         this.chartSeries = this.props.series.map(({ key, name, valueField, argumentField, color, show, type, scaleName }, index) => (
            {
                key: name,
                name,
                valueField: valueField,
                argumentField: argumentField || this.props.category,
                color: color || palette[index % palette.length],
                scaleName,
                seriesComponent: SplineWithPoints,
                pointComponent: this.SplinePoint
            }
        )).filter(t => this.state.shows[t.name])

        this.seriesList = this.chartSeries.map(t => React.createElement(SplineSeries, t))
         */

        this.seriesList = this.props.series.map(({ key, name, valueField, argumentField, color, show, type, scaleName }, index) => {
            if (!this.state.shows[name])
                return null
            const props: any = {
                key: name,
                name,
                valueField: valueField,
                argumentField: argumentField || this.props.category,
                color: color || palette[index % palette.length],
                scaleName,
            }
            let seriesType
            type = type || this.props.type

            console.log('chart', type)
            if (type == 'bar') {
                seriesType = BarSeries
                props.pointComponent = this.BarPoint
            } else if (type === 'area') {
                seriesType = AreaSeries
                props.seriesComponent = AreaWithPoints;
                props.pointComponent = this.AreaPoint;
            } else {
                seriesType = SplineSeries
                props.seriesComponent = SplineWithPoints
                props.pointComponent = this.SplinePoint
            }
            return React.createElement(seriesType, props)
        })
        this.seriesList.length = 32
    }

    render() {
        if (Array.isArray(this.props.data)) this.data = this.props.data
        else if (this.props.tabs) this.data = this.props.data[this.props.tabs[this.state.tab].valueField]

        const locale = getLocale()
        //console.log('chartrender', this.data, this.series)

        const defaultViewport = { argumentStart: (this.data[this.data.length - 12] || this.data[0])?.[this.props.category as any], argumentEnd: this.data[this.data.length - 1]?.[this.props.category as any] }
        //const classes = useStyles();
        //{this.props.zoom ? null : <ZoomAndPan defaultViewport={defaultViewport} />}
        this.createSeries()
        return (
            <CardContent>
                <div >
                    {this.props.tabs?.map((t, index) =>
                        <Chip
                            size="small"
                            color={this.state.tab == index ? "primary" : "default"}
                            onClick={() => { this.setState({ tab: index }) }}
                            label={t.name}
                        />
                    )}
                </div>
                <FormGroup row>
                    {this.props.series?.map((t, index) =>
                        <FormControlLabel
                            control={<Checkbox
                                icon={<CheckBoxIcon />}
                                checkedIcon={<CheckBoxIcon />}
                                size="small"
                                style={{ color: t.color || palette[index % palette.length] }}
                                checked={this.state.shows[t.name]} onChange={() => { this.switchShow(t.name) }} />}
                            label={t.name}
                            style={{ opacity: this.state.shows[t.name] ? 1 : 0.5 }}
                        />
                    )}
                </FormGroup>
                <ReactChart data={this.data} rotated={this.props.rotated}>
                    <EventTracker />
                    <ArgumentAxis tickFormat={this.props.argumentFormat} />
                    <ValueAxis tickFormat={this.props.valueFormat || (locale == 'zh' ? (obj => formatW) : (obj => formatK))} />
                    <HoverState hover={this.state.target} onHoverChange={this.changeHover} />
                    <Tooltip targetItem={this.state.target} contentComponent={this.TooltipContent} />
                    <Animation />
                    {this.seriesList}
                    <Stack />
                </ReactChart>
            </CardContent>
        );
    }
}
