import * as React from "react";
import {
    List, Datagrid, TextField, EditButton, Edit,
    ListProps, TextInput, NumberField, BooleanField, BooleanInput, NumberInput, ArrayInput, SimpleFormIterator, DateField, DateTimeInput, FormTab, TabbedForm, AutocompleteArrayInput, FunctionField,
} from 'react-admin';
import { Typography, Box } from '@material-ui/core';
import { useMediaQuery } from '@material-ui/core';
import { TextInputs, NumberInputs } from '../../components/MyComponents';
import icon from '@material-ui/icons/Flag';
import get from 'lodash/get';

const userFilters = [
    <TextInput source="name" alwaysOn allowEmpty />,
    <TextInput source="intro" allowEmpty />,
    <TextInput source="id" allowEmpty />,
];

//creater: "610cf1c11a5b1bf46c5ad531"
//joinRequests: []
//members: [{id: "610cf1c11a5b1bf46c5ad531", name: "ink001", showHero: 10039, type: 1,}]

const ResList = (props: ListProps) => (
    <List filters={userFilters} {...props}>
        <Datagrid>
            <TextField source="id" />
            <TextField source="name" />
            <TextField source="flag" />
            <TextField source="intro" />
            <NumberField source="joinType" />
            <FunctionField source="membersCount" textAlign="right" render={(record, source) => {
                const clients = get(record, source) as string
                const maxClients = get(record, "membersMax") as string
                return clients + "/" + maxClients
            }} />
            <NumberField source="seasonScore" />
            <NumberField source="memberSeasonScoreReq" />
            <DateField source="createdAt" showTime />
            <EditButton />
        </Datagrid>
    </List>
);

const GuildTitle = ({ record }) => {
    return <span>Guild {record ? `"${record.id}:${record.name}"` : ''}</span>;
};

const Aside = ({ record }) => (
    <div style={{ width: 200, margin: '1em' }}>
        <Typography variant="h6">Guild details</Typography>
        {record && (
            <Typography variant="body2">
                Updated date: {record.updatedAt}
            </Typography>
        )}
    </div>
);

const ResEdit = props => {
    const isSmall = useMediaQuery((theme: any) => theme.breakpoints.down('sm'));
    return (
        <Edit title={<GuildTitle {...props} />} aside={isSmall ? null : <Aside {...props} />}  {...props}>

            <TabbedForm>
                <FormTab label="summary">
                    <TextInput source="id" />
                    <TextInput source="name" />
                    <TextInput source="flag" />
                    <TextInput source="intro" />
                    <NumberInput source="joinType" />
                    <NumberInputs keys={['membersCount', 'membersMax']} />
                    <NumberInputs keys={['seasonScore', 'memberSeasonScoreReq']} />
                    <DateTimeInput source="createdAt" />
                </FormTab>
                <FormTab label="members">
                    <ArrayInput source="members"><SimpleFormIterator>
                        <TextInput source="id" />
                        <TextInput source="name" />
                        <NumberInput source="seasonScore" />
                        <NumberInput source="showHero" />
                        <NumberInput source="type" />
                        <DateTimeInput source="onlineTime" />
                    </SimpleFormIterator></ArrayInput>
                </FormTab>
                <FormTab label="joinRequests">
                </FormTab>
            </TabbedForm>
        </Edit>
    );
}

export default { name: "guild", list: ResList, edit: ResEdit, icon }