import * as React from "react";

import {
    List, Datagrid, TextField, EditButton, Edit,
    /*ReferenceField, SimpleForm, ReferenceInput, SelectInput, Create, SimpleList, DateInput,*/
    ListProps, TextInput, NumberField, BooleanField, BooleanInput, NumberInput, ArrayInput, SimpleFormIterator, DateField, DateTimeInput, FormTab, TabbedForm, AutocompleteArrayInput,
} from 'react-admin';
//import MyUrlField from './MyUrlField';
import { /*Accordion, AccordionDetails, AccordionSummary,*/ Typography, Box } from '@material-ui/core';
import { useMediaQuery } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import { TextInputs, NumberInputs, BooleanInputs, DateTimeInputs, ElapsedTimeField } from '../../components/MyComponents';
import { makeStyles } from '@material-ui/core/styles';

import icon from '@material-ui/icons/Group';

const userFilters = [
    <TextInput source="nickname" alwaysOn allowEmpty />,
    <TextInput source="id" allowEmpty />,
    <TextInput source="ip" allowEmpty />,
    <TextInput source="region" allowEmpty />,
    <TextInput source="devices.id" allowEmpty />,
];

const ResList = (props: ListProps) => (
    <List filters={userFilters} {...props}>
        <Datagrid>
            <TextField source="id" />
            <TextField source="nickname" />
            <TextField source="ip" />
            <TextField source="region" />
            <NumberField source="metadata.seasonLevel" />
            <NumberField source="metadata.coin" />
            <NumberField source="metadata.diamond" />
            <BooleanField source="metadata.isVip" />
            <BooleanField source="metadata.isNewFace" />
            <BooleanField source="isAnonymous" />
            <ElapsedTimeField textAlign='right' source="createdAt" />
            <ElapsedTimeField textAlign='right' source="updatedAt" />
            <EditButton />
        </Datagrid>
    </List>
);

const UserTitle = ({ record }) => {
    return <span>User {record ? `"${record.id}:${record.nickname}"` : ''}</span>;
};

const Aside = ({ record }) => (
    <div style={{ width: 200, margin: '1em' }}>
        <Typography variant="h6">User details</Typography>
        {record && (
            <Typography variant="body2">
                Updated date: {record.updatedAt}
            </Typography>
        )}
    </div>
);

const ResEdit = props => {
    const isSmall = useMediaQuery((theme: any) => theme.breakpoints.down('sm'));
    return (
        <Edit title={<UserTitle {...props} />} aside={isSmall ? null : <Aside {...props} />}  {...props}>
            <TabbedForm>
                <FormTab label="summary" >
                    <TextInputs keys={['id', 'nickname']} />
                    <TextInputs keys={['ip', 'region']} />
                    <TextInputs source="devices" keys={['id', 'platform']} />
                    <BooleanInputs keys={['metadata.isVip', 'metadata.isNewFace', 'isAnonymous']} />
                    <NumberInputs xs={6} source="metadata" keys={['coin', 'diamond']} />
                    <NumberInputs source="metadata" keys={['seasonLevel', 'seasonScore', 'seasonHightScore']} />
                    <NumberInputs source="metadata" keys={['seasonRewardLevel', 'seasonRewardScore', 'seasonRewardUnlockLevel']} />
                    <DateTimeInputs keys={['createdAt', 'deleteAt']} />
                    <DateTimeInputs keys={['metadata.loginAt', 'metadata.offlineAt']} />


                </FormTab>
                <FormTab label="items">
                    <ArrayInput source="metadata.bagList"><SimpleFormIterator>
                        <NumberInputs xs={4} keys={['id', 'state', 'openTime']} />
                    </SimpleFormIterator></ArrayInput>
                </FormTab>
                <FormTab label="heros">
                    <NumberInput source="metadata.teamSelected" />
                    <ArrayInput source="metadata.teams"><SimpleFormIterator>
                        <ArrayInput source="metadata.teams"><SimpleFormIterator>
                            <NumberInput source="" />
                        </SimpleFormIterator></ArrayInput>
                    </SimpleFormIterator></ArrayInput>

                    <ArrayInput source="metadata.heroList"><SimpleFormIterator>
                        <NumberInputs xs={4} keys={['id', 'level', 'card']} />
                    </SimpleFormIterator></ArrayInput>

                    <ArrayInput source="metadata.talents"><SimpleFormIterator>
                        <NumberInput source="" />
                    </SimpleFormIterator></ArrayInput>

                    <NumberInput source="metadata.useFightBg" />
                    <ArrayInput source="metadata.useBqs"><SimpleFormIterator>
                        <NumberInput source="" />
                    </SimpleFormIterator></ArrayInput>
                </FormTab>
                <FormTab label="other">
                    <NumberInput source="metadata.power" />
                    <NumberInput source="metadata.matchPower" />
                    <NumberInput source="metadata.loginDays" />
                    <NumberInput source="metadata.allOpenBags" />
                    <NumberInput source="metadata.allVictorys" />
                    <NumberInput source="metadata.allBattles" />
                    <NumberInput source="metadata.continuousLossCount" />
                    <NumberInput source="metadata.cooperationGetGiftBit" />
                    <NumberInput source="metadata.cooperationNum" />
                    <NumberInput source="metadata.dailyActivity" />
                    <NumberInput source="metadata.dailyActivityLevel" />
                    <NumberInput source="metadata.genericMarks" />
                </FormTab>
            </TabbedForm>
        </Edit>
    );
}

export default { name: "user", list: ResList, edit: ResEdit, icon }