import * as React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import { useTranslate, useLocale, useSetLocale, Title } from 'react-admin';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import CardActions from '@material-ui/core/CardActions';
//import { changeTheme } from './actions';
import { AppState } from '../types';

import { ThemeName } from '../types';
import { getMomentLocale, languageNames } from '../components/polyglot';
import { changeTheme, GetTheme } from '../themeReducer';
import themes from '../themes';
import moment from 'moment';

const useStyles = makeStyles({
    label: { width: '10em', display: 'inline-block' },
    button: { margin: '1em' },
});

const Configuration = () => {
    const translate = useTranslate();
    const locale = useLocale();
    const _setLocale = useSetLocale();
    const setLocale = (value: string) => {
        if (value == locale) return
        localStorage.setItem('locale', value)
        moment.locale(value)
        return _setLocale(value)
    }

    const classes = useStyles();
    const theme = GetTheme();
    const dispatch = useDispatch();
    return (
        <Box>
            <Card>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                        {translate('pos.theme.name')}
                    </Typography>
                    <Typography >
                        {Object.entries(themes).map(t =>
                            <Button
                                variant="contained"
                                className={classes.button}
                                color={theme === t[0] ? 'primary' : 'default'}
                                onClick={() => dispatch(changeTheme(t[0] as ThemeName))}
                            >
                                {translate(`pos.theme.${t[0]}`)}
                            </Button>)
                        }
                    </Typography>
                </CardContent>
            </Card>

            <Card>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                        {translate('pos.language')}
                    </Typography>
                    <Typography >
                        {Object.entries(languageNames).map(t =>
                            <Button
                                variant="contained"
                                className={classes.button}
                                color={locale === t[0] ? 'primary' : 'default'}
                                onClick={() => setLocale(t[0])}
                            >
                                {t[1]}
                            </Button>)
                        }
                    </Typography>
                </CardContent>
            </Card>
        </Box >
    );
};

export default Configuration;
