import * as React from 'react';
import { Route } from 'react-router-dom';
import Configuration from './pages/configuration';
import Users from './pages/analytics/users';
import Realtime from './pages/analytics/realtime';
import Events from './pages/analytics/events';
import Funnels from './pages/analytics/funnels';
import Personas from './pages/analytics/personas';
import Keys from './pages/analytics/keys';
import AppConfig from './pages/appConfig';
//import Segments from './segments/Segments';

export default [
    <Route exact path="/configuration" render={() => <Configuration />} />,
    <Route exact path="/appConfig" render={() => <AppConfig />} />,
    <Route exact path="/analytics/users" render={(props) => <Users {...props} />} />,
    <Route exact path="/analytics/realtime" render={(props) => <Realtime {...props} />} />,
    <Route exact path="/analytics/events" render={(props) => <Events {...props} />} />,
    <Route exact path="/analytics/funnels" render={(props) => <Funnels {...props} />} />,
    <Route exact path="/analytics/personas" render={(props) => <Personas {...props} />} />,
    <Route exact path="/analytics/keys" render={() => <Keys />} />,
    //<Route exact path="/segments" render={() => <Segments />} />,
];
