import * as React from "react";
import { Card, CardContent, CardHeader, Container, Box, Grid, Typography } from '@material-ui/core';
//import Tabs from '@mui/material/Tabs';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import CardMedia from '@material-ui/core/CardMedia';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import Slider from '@material-ui/core/Slider';
import { NumberField, NumberInput, useTranslate } from "react-admin";
import RoomIcon from '@material-ui/icons/SportsEsports';
import CpuIcon from '@material-ui/icons/Memory';
import MemoryIcon from '@material-ui/icons/Storage';
import UserIcon from '@material-ui/icons/Group';
import GuildIcon from '@material-ui/icons/Flag';
import RecordIcon from '@material-ui/icons/History';
import ReportLogIcon from '@material-ui/icons/BugReport';
import { withStyles, makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import { red, green, blue, yellow, deepOrange, orange } from '@material-ui/core/colors';
import { adminApi, appApi } from "../providers/dataProvider";
import MyChart from "../components/MyChart";
import MyMonitor from "../components/MyMonitor";
import MyInfo from "../components/MyInfo";
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import { format } from "d3-format";
import { getDayString, getDayFullString } from "../utils";
import moment from "moment";
import RetentionTable from "../components/RetentionTable";
import { useMyContext } from "../providers/contextProvider";

const formatCount = format(",")
const formatDoD = format("+.2%")

export default () => {
    const myContext = useMyContext()
    const [state, setState] = React.useState<{ yesterdayData: any[], chartActiveData: any[] }>({
        yesterdayData: [],
        chartActiveData: [],
    });

    React.useEffect((async () => {
        if (!adminApi.token) return
        const dayEnd = moment.utc().startOf('day').unix() / 86_400
        const dayBegin = dayEnd - 30
        const body: any = {
            dayEnd,
            dayBegin,
            values: ['new', 'active', 'count', ...[1, 2, 3, 4, 5, 6, 7, 14, 30].map(t => `new_d${t}`)],
        };

        const chartActiveData = await adminApi.post("analytics", body)
        const yesterdayItem = chartActiveData.find(t => t.day == dayEnd - 1)
        const twoDaysAgoItem = chartActiveData.find(t => t.day == dayEnd - 2)
        const yesterdayData = [{
            name: 'pos.dayInfo.active',
            value: formatCount(yesterdayItem?.active ?? 0),
            dod: ((yesterdayItem?.active ?? 0) - (twoDaysAgoItem?.active ?? 0)) / (twoDaysAgoItem?.active || 1)
        }, {
            name: 'pos.dayInfo.new',
            value: formatCount(yesterdayItem?.new ?? 0),
            dod: ((yesterdayItem?.new ?? 0) - (twoDaysAgoItem?.new ?? 0)) / (twoDaysAgoItem?.new || 1)
        }, {
            name: 'pos.dayInfo.earn',
            value: formatCount(0),
            dod: 0,
        }];

        setState({ yesterdayData, chartActiveData })
    }) as any, []);

    const translate = useTranslate();
    return (
        <Box >
            <Container maxWidth="xl">
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Card>
                            <CardHeader title={translate('pos.dayInfo.name')} subheader={translate('pos.dayInfo.time', {
                                time: moment().utc().startOf('day').local().format('YYYY-MM-DD HH:mm')
                            })} />
                            <CardContent>
                                <Grid container spacing={1}>
                                    {state.yesterdayData.map(t =>
                                        <Grid item xs={4} >
                                            <Typography color="textSecondary" variant="overline" gutterBottom>{translate(t.name)}</Typography>
                                            <Box sx={{ alignItems: 'center', display: 'flex' }}>
                                                <Typography color="textPrimary" variant="h5">{t.value}</Typography>
                                                {t.dod < 0 ? <ArrowDownwardIcon color="error" /> : <ArrowUpwardIcon color="primary" />}
                                            </Box>
                                            <Box sx={{ alignItems: 'center', display: 'flex', pt: 2 }}>
                                                <Typography variant="caption" color="textSecondary">{translate('pos.dayInfo.dod', { value: formatDoD(t.dod) })}</Typography>
                                            </Box>
                                        </Grid>)}
                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                    {myContext.state.config.roomConsoleUrl ? <Grid item xs={12}><MyMonitor /></Grid> : null}
                    <Grid item xs={12}>
                        <Card>
                            <CardHeader title={translate('pos.activeTrend')} />
                            <MyChart data={state.chartActiveData}
                                series={[
                                    { name: translate('pos.chart.new'), valueField: "new" },
                                    { name: translate('pos.chart.active'), valueField: "active" },
                                    { name: translate('pos.chart.count'), valueField: "count" },
                                ]}
                                category={'day'}
                                argumentFormat={obj => getDayString}
                                tooltipTitleFormat={getDayFullString} />
                        </Card>
                    </Grid>
                    <Grid item xs={12}>
                        <Card>
                            <CardHeader title={translate('pos.retentionTrend')} />
                            <RetentionTable data={state.chartActiveData} />
                        </Card>
                    </Grid>
                </Grid >
            </Container>
        </Box >
    );
}