import * as React from "react";
import { Admin, Resource, /*ListGuesser, EditGuesser,*/ Layout } from 'react-admin';
import { useRecordContext } from 'react-admin';
import LaunchIcon from '@material-ui/icons/Launch';

import AppSwitcher from '../components/AppSwitcher';
import UserIcon from '@material-ui/icons/Group';
import { Typography, Box } from '@material-ui/core';
import { forwardRef } from 'react';
import { AppBar, UserMenu, MenuItemLink, useTranslate } from 'react-admin';
import SettingsIcon from '@material-ui/icons/Settings';
import { makeStyles } from '@material-ui/core/styles';

import Logo from './Logo';

const useStyles = makeStyles({
    title: {
        flex: '1',
        overflow: 'hidden',
        'white-space': 'nowrap',
        'text-overflow': 'ellipsis',
    },
    spacer: {
        flex: 1,
    },
});

const ConfigurationMenu = forwardRef<any, any>((props, ref) => {
    const translate = useTranslate();
    return (
        <MenuItemLink
            ref={ref}
            to="/configuration"
            primaryText={translate('pos.configuration')}
            leftIcon={<SettingsIcon />}
            onClick={props.onClick}
            sidebarIsOpen
        />
    );
});

const CustomUserMenu = (props: any) => (
    <UserMenu {...props}>
        <ConfigurationMenu />
    </UserMenu>
);

const CustomAppBar = props => {
    const classes = useStyles();
    //    <LocaleSwitcher />
    return <AppBar {...props} elevation={1} userMenu={<CustomUserMenu />}>
        <Typography className={classes.title} variant="h6" id="react-admin-title"></Typography>
        <span className={classes.spacer} />
        <AppSwitcher />
    </AppBar>
}

export default CustomAppBar;