import * as React from "react";
import {
    List, Datagrid, TextField, EditButton, Edit,
    ListProps, TextInput, NumberField, BooleanField, BooleanInput, NumberInput, ArrayInput, SimpleFormIterator, DateField, DateTimeInput, FormTab, TabbedForm, AutocompleteArrayInput, ArrayField, SingleFieldList, ChipField, FunctionField, SimpleForm, useRefresh,
} from 'react-admin';
import { Typography, Box } from '@material-ui/core';
import { useMediaQuery } from '@material-ui/core';
import { TextInputs, NumberInputs, TagsField, ElapsedTimeField } from '../../components/MyComponents';
import get from 'lodash/get';
import { useEffect, useRef } from "react";
import icon from '@material-ui/icons/SportsEsports';

const userFilters = [
    <TextInput source="roomId" alwaysOn allowEmpty />,
    <TextInput source="name" allowEmpty />,
    <TextInput source="version" allowEmpty />,
    <NumberInput source="seasonLevel" allowEmpty />,
];

function useRecursiveTimeout(callback, delay) {
    const savedCallback = useRef(callback)
    useEffect(() => { savedCallback.current = callback }, [callback])
    useEffect(() => {
        let id
        function tick() {
            const ret = savedCallback.current()
            if (ret instanceof Promise) {
                ret.then(() => { id = setTimeout(tick, delay) })
            } else id = setTimeout(tick, delay)
        }
        if (delay !== null) {
            id = setTimeout(tick, delay)
            return () => id && clearTimeout(id)
        }
    }, [delay])
}

const ResList = (props: ListProps) => {
    //const refresh = useRefresh()
    //useRecursiveTimeout(() => refresh(), 2000)

    return (<List filters={userFilters} {...props}>
        <Datagrid>
            <TextField source="roomId" />
            <TextField source="name" />
            <FunctionField source="users" textAlign="right" render={(record, source) => {
                const users = get(record, source) as any[]
                return users.map(t => t.isAI ? 'AI' : t.nickname).join(' | ')
            }} />
            <TextField source="processId" />
            <TextField source="version" />
            <NumberField source="seasonLevel" />
            <BooleanField source="locked" />
            <ElapsedTimeField textAlign='right' source="updatedAt" />
            <ElapsedTimeField textAlign='right' source="createdAt" label="elapsedTime" />
            <EditButton />
        </Datagrid>
    </List>)
}


const RoomTitle = ({ record }) => {
    return <span>Room {record ? `"${record.name}:${record.roomId}"` : ''}</span>;
};

const Aside = ({ record }) => (
    <div style={{ width: 200, margin: '1em' }}>
        <Typography variant="h6">Room details</Typography>
        {record && (
            <Typography variant="body2">
                Updated date: {record.updatedAt}
            </Typography>
        )}
    </div>
);

const ResEdit = props => {
    const isSmall = useMediaQuery((theme: any) => theme.breakpoints.down('sm'));
    return (
        <Edit title={<RoomTitle {...props} />} aside={isSmall ? null : <Aside {...props} />}  {...props}>
            <SimpleForm>
                <TextInput source="roomId" />
                <TextInput source="name" />
                <TextInput source="processId" />
                <TextInput source="version" />
                <NumberInput source="seasonLevel" />
                <NumberInputs keys={['clients', 'maxClients']} />
                <BooleanInput source="locked" />
                <BooleanInput source="private" />
                <BooleanInput source="unlisted" />
                <DateTimeInput source="createdAt" />
            </SimpleForm>
        </Edit>
    );
}
//create: VisitorCreate,
export default { name: "roomCache", list: ResList, edit: ResEdit, icon }