import React, { PureComponent } from 'react'
import { Card, CardContent, CardHeader, Box, Avatar, Grid, Typography } from '@material-ui/core';
import { NumberField, NumberInput, useTranslate } from "react-admin";
import RoomIcon from '@material-ui/icons/SportsEsports';
import CpuIcon from '@material-ui/icons/Memory';
import MemoryIcon from '@material-ui/icons/Storage';
import ReportLogIcon from '@material-ui/icons/BugReport';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import { red, green, blue, yellow, deepOrange, orange } from '@material-ui/core/colors';
import { appApi } from "../providers/dataProvider";

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            '& > *': {
                margin: theme.spacing(1),
            },
        },
        card: {
            //borderRadius: 12,
            //textAlign: 'right',
            padding: theme.spacing(1),
        },
        room: {
            color: theme.palette.getContrastText(green[600]),
            backgroundColor: green[600],
            width: theme.spacing(7),
            height: theme.spacing(7),
        },
        cpu: {
            color: theme.palette.getContrastText(blue[500]),
            backgroundColor: blue[500],
            width: theme.spacing(7),
            height: theme.spacing(7),
        },
        memory: {
            color: theme.palette.getContrastText(orange[800]),
            backgroundColor: orange[800],
            width: theme.spacing(7),
            height: theme.spacing(7),
        },
        bug: {
            color: theme.palette.getContrastText(red[500]),
            backgroundColor: red[500],
            width: theme.spacing(7),
            height: theme.spacing(7),
        },
        rounded: {
            color: '#fff',
            backgroundColor: green[500],
        },
        small: {
            width: theme.spacing(3),
            height: theme.spacing(3),
        },
        icon: {
            width: theme.spacing(5),
            height: theme.spacing(5),
        },
    }),
);

interface State {
    rooms: number;
    bugs: number;
    cpu: number;
    memory: { totalMemMb: number, usedMemMb: number };
}

function renderSize(value: number, unit: string) {
    if (unit == 'KB' && value > 100) {
        unit = 'MB'
        value /= 1024
    }
    if (unit == 'MB' && value > 100) {
        unit = 'GB'
        value /= 1024
    }
    if (unit == 'GB' && value > 100) {
        unit = 'TB'
        value /= 1024
    }
    return value.toFixed(2) + ' ' + unit;
}

const UPDATE_ROOM_LIST_INTERVAL = 5000;

const MyMonitor = () => {
    const [state, setState] = React.useState<{ icon: any, className: string, value: string | number, second: string }[]>([]);

    const classes = useStyles();
    const fetchDashboard = async () => {
        var res = await appApi.post("dashboard") as {
            rooms: number;
            bugs: number;
            cpu: number;
            memory: { totalMemMb: number, usedMemMb: number };
        }
        setState([{ icon: RoomIcon, className: classes.room, value: res.rooms, second: 'pos.dashboard.rooms' },
        { icon: CpuIcon, className: classes.cpu, value: res.cpu + '%', second: 'pos.dashboard.cpu' },
        { icon: MemoryIcon, className: classes.memory, value: renderSize(res.memory.usedMemMb, 'MB'), second: 'pos.dashboard.memory' },
        { icon: ReportLogIcon, className: classes.bug, value: res.bugs, second: 'pos.dashboard.bugs' }])
    }

    React.useEffect(() => {
        fetchDashboard()
        const timer = setInterval(fetchDashboard, UPDATE_ROOM_LIST_INTERVAL);
        return () => clearInterval(timer);//
    }, []);

    const translate = useTranslate();
    return (
        <Grid container spacing={1}>
            {state.map(it => (<Grid item xs={6} sm={6} md={3}>
                <Card className={classes.card}>
                    <Grid container spacing={1}>
                        <Grid item xs={5} >
                            <Avatar variant="rounded" className={it.className}>
                                <it.icon className={classes.icon} />
                            </Avatar>
                        </Grid>
                        <Grid item xs={7} >
                            <Typography component="h6" variant="h6">{it.value}</Typography>
                            <Typography variant="subtitle1" color="textSecondary">{translate(it.second)}</Typography>
                        </Grid>
                    </Grid>
                </Card>
            </Grid>))}
        </Grid>
    )
}
export default MyMonitor