import * as React from "react";
import { Card, CardContent, CardHeader, Container, Box, Grid, TextField, Button } from '@material-ui/core';
import { useTranslate } from "react-admin";
import moment from "moment";
import { adminApi } from "../providers/dataProvider";
import MyChart from "../components/MyChart";
import AnalyticsQuery from "../components/AnalyticsQuery";
import RetentionTable from "../components/RetentionTable";
import { getDayString } from "../utils";
import ReactJson, { InteractionProps } from "react-json-view";
import { useMyContext } from "../providers/contextProvider";
import SaveIcon from '@material-ui/icons/Save';

export default () => {
    const { state, setConfig } = useMyContext()
    const [saveEnable, setSaveEnable] = React.useState(false)
    const ref = React.useRef<object | null>(null)

    const handleSaveClick = async () => {
        const config = ref.current
        console.log(config)
        await adminApi.post("setConfig", { config })
        setConfig(config)
        setSaveEnable(false)
    }
    const handleJsonChange = (edit: InteractionProps) => {
        console.log(ref.current)
        ref.current = edit.updated_src
        console.log(ref.current)
        setSaveEnable(true)
    }
    const translate = useTranslate();
    return (
        <Box >
            <Container maxWidth="xl">
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Card>
                            <CardHeader title={translate('pos.appConfigEdit')}
                                action={
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        size="large"
                                        onClick={handleSaveClick}
                                        disabled={!saveEnable}
                                        startIcon={<SaveIcon />}
                                    > Save </Button>}
                            />
                            <CardContent>
                                <ReactJson src={state.config} theme="railscasts"
                                    onEdit={(edit) => handleJsonChange(edit)}
                                    onAdd={(edit) => handleJsonChange(edit)}
                                    onDelete={(edit) => handleJsonChange(edit)}
                                    displayDataTypes={false}
                                    collapsed={1}
                                />
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </Container>
        </Box >
    );
}