import { Reducer } from 'redux';
//import { CHANGE_THEME, changeTheme } from './configuration/actions';
import { ThemeName } from './types';
import { useSelector } from 'react-redux';
import { AppState } from './types';

const CHANGE_THEME = 'CHANGE_THEME';

export const changeTheme = (theme: ThemeName) => ({
    type: CHANGE_THEME,
    payload: theme,
});

export const GetTheme = (): ThemeName => {
    return useSelector((state: AppState) => state.theme || localStorage.getItem('theme') as ThemeName || 'dark')
}

type State = ThemeName | undefined | null;
type Action =
    | ReturnType<typeof changeTheme>
    | { type: 'OTHER_ACTION'; payload?: any };

const themeReducer: Reducer<State, Action> = (
    previousState,
    action
) => {
    //console.log('themeReducerx', previousState, action.type, action.payload)
    if (action.type === CHANGE_THEME) {
        localStorage.setItem('theme', action.payload)
        return action.payload;
    }
    return previousState || null
};

export default themeReducer;
