import * as React from "react";
import {
    List, Datagrid, TextField, EditButton, Edit,
    ListProps, TextInput, NumberField, BooleanField, BooleanInput, NumberInput, ArrayInput, SimpleFormIterator, DateField, DateTimeInput, FormTab, TabbedForm, AutocompleteArrayInput, FunctionField, SimpleForm,
} from 'react-admin';
import { Typography, Box } from '@material-ui/core';
import { useMediaQuery } from '@material-ui/core';
import { TextInputs, NumberInputs } from '../../components/MyComponents';
import get from 'lodash/get';
import icon from '@material-ui/icons/Assessment';

const userFilters = [
    <TextInput source="name" alwaysOn allowEmpty />,
    <TextInput source="intro" allowEmpty />,
    <TextInput source="id" allowEmpty />,
];

//creater: "610cf1c11a5b1bf46c5ad531"
//joinRequests: []
//members: [{id: "610cf1c11a5b1bf46c5ad531", name: "ink001", showHero: 10039, type: 1,}]

const ResList = (props: ListProps) => (
    <List filters={userFilters} {...props}>
        <Datagrid>
            <TextField source="name" />
            <NumberField source="total" />
            <FunctionField source="top" render={(record, source) => JSON.stringify(get(record, source).slice(0, 10)?.map(t => t.name))} label="top10" />
            <FunctionField source="top" sortable={false} render={(record, source) => JSON.stringify(get(record, source)[0]?.score)} label="highest" />
            <FunctionField source="top" sortable={false} render={(record, source) => JSON.stringify(get(record, source).slice(-1)[0]?.score)} label="lowest" />
            <EditButton />
        </Datagrid>
    </List>
);

const LeaderboardTitle = ({ record }) => {
    return <span>Leaderboard {record ? `"${record.name}"` : ''}</span>;
};

const Aside = ({ record }) => (
    <div style={{ width: 200, margin: '1em' }}>
        <Typography variant="h6">Leaderboard details</Typography>
        {record && (
            <Typography variant="body2">
                Updated date: {record.updatedAt}
            </Typography>
        )}
    </div>
);

const ResEdit = props => {
    const isSmall = useMediaQuery((theme: any) => theme.breakpoints.down('sm'));
    return (
        <Edit title={<LeaderboardTitle {...props} />} aside={isSmall ? null : <Aside {...props} />}  {...props}>
            <SimpleForm>
                <TextInput source="name" />
                <NumberInput source="total" />
                <ArrayInput source="top"><SimpleFormIterator>
                    <TextInputs xs={4} keys={['id', 'name', 'score']} />
                </SimpleFormIterator></ArrayInput>
            </SimpleForm>
        </Edit>
    );
}

export default { name: "leaderboard", list: ResList, edit: ResEdit, icon }