import React, { useState, useContext } from "react";
import { adminApi, appApi } from "./dataProvider";
import lodash from 'lodash';

const myContext = React.createContext({} as any);

export function useMyContext() {
    return useContext(myContext)
}

export const ContextProvider = props => {
    const [state, _setState] = React.useState<{
        defaultApp: string | null;
        apps: { name: string, usedMemMb: number }[];
        config: any
        keys: any
        clientKeys?: any
        flatKeys?: any
    }>({
        defaultApp: localStorage.getItem('defaultApp'),
        apps: [],
        config: {},
        keys: {},
        clientKeys: undefined,
        flatKeys: undefined
    });
    adminApi.setProfile('appName', state.defaultApp)

    const setState = (value: any) => {
        Object.assign(state, value)
        _setState({ ...state });
    }
    const setDefaultApp = (defaultApp: string | null) => {
        if (state.defaultApp == defaultApp) return
        if (defaultApp) localStorage.setItem('defaultApp', defaultApp)
        else localStorage.removeItem('defaultApp')
        adminApi.setProfile('appName', defaultApp)
        setState({ defaultApp });
    }

    const setApps = (apps: { name: string, usedMemMb: number }[]) => {
        if (!state.defaultApp) setDefaultApp(apps[0].name)
        setState({ apps });
        console.log("xxx2setApps")
    }
    
    const setConfig = (config: any) => {
        appApi.apiUrl = config.roomConsoleUrl
        setState({ config });
    }

    const setKeys = (keys: any) => {
        function initializeCount(obj) {
            obj._childCount = 0
            obj._allChildCount = 0
            for (const key in obj) {
                if (key.startsWith('_')) continue
                const it = obj[key]
                if (!it) {
                    obj._childCount++
                    obj._allChildCount++
                } else if (it._enable === false) {
                    delete obj[key]
                } else {
                    initializeCount(it)
                    obj._childCount++
                    obj._allChildCount += it._allChildCount
                }
            }
        }
        /**  */
        function flattenPaths(object: any, root: string) {
            const result: any[] = []
            function flat(obj, path: string) {
                for (const key in obj) {
                    if (key.startsWith('_')) continue
                    const itemPath = path + key
                    const it = obj[key]
                    if (!it) result.push(itemPath)
                    else if (it._enable !== false) {
                        if (it._allChildCount == 0)
                            result.push(itemPath)
                        else
                            flat(it, `${itemPath}.`)
                    }
                }
            }
            flat(object, root)
            return result
        }
        function initializeValues(object: any) {
            let minFlatCount = 1
            function expand(obj, path: string) {
                minFlatCount += obj._childCount - 1
                for (const key in obj) {
                    if (key.startsWith('_')) continue
                    const itemPath = path + key
                    const it = obj[key]
                    if (!it || it._childCount > 12) { }
                    else if (it._childCount > 1 && it._expand !== true && minFlatCount > 1 && it._childCount == it._allChildCount && it._childCount <= 12) // 
                        it._children = flattenPaths(it, `${itemPath}.`)// 
                    else if (it._childCount <= 32) expand(it, `${itemPath}.`) // 
                }
            }
            expand(object, '')
        }
        function initialize(obj) {
            initializeCount(obj)
            initializeValues(obj)
        }

        const clientKeys = lodash.cloneDeep(keys)
        for (const key in clientKeys) initialize(clientKeys[key])
        //const clientKeys = lodash.mapValues(keys, getClientKeys)
        //console.log('xxxxxxxxxxxxxxx', clientKeys)

        const getKeysList = (object: any) => {
            const result: any[] = []
            function flat(obj, path: string) {
                for (const key in obj) {
                    if (key.startsWith('_')) continue
                    const itemPath = path + key
                    const it = obj[key]
                    if (!it) result.push(itemPath)
                    else if (it._childCount > 32) continue
                    else if (it._childCount == 0 || it._children) result.push(itemPath)
                    else flat(it, `${itemPath}.`)
                }
            }
            flat(object, '')
            return result
        }

        const flatKeys = {}
        for (const key in clientKeys) flatKeys[key] = getKeysList(clientKeys[key])
        setState({ keys, clientKeys, flatKeys });
    }

    return (
        <myContext.Provider value={{ state, setDefaultApp, setApps, setKeys, setConfig, setState }}>
            {props.children}
        </myContext.Provider>
    )
}